@import url(//fonts.googleapis.com/css?family=Open+Sans);
@import url(//fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  --white: #f5f5f5;
  --black: #1b1d1e; /* Other Options: #161718, #242424 */
  --type-color: #5eeb5b; /* Other Options: #ff5252, #2274a5 */
  font-family: 'Open Sans', 'PT Sans', sans-serif;
}

.home,
.aboutMe,
.skills,
.projects {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  background-color: var(--white);
}

.navbar {
  /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
  background-color: var(--black);
  background-color: #161718;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: var(--white);
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-compact-disc {
  margin-right: 0.5rem;
  font-size: 2.2rem;
}

.fa-compact-disc:hover {
  color: var(--type-color);
  transition: all 0.2s ease-out;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: var(--white);
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  color: var(--type-color);
  border-bottom: 4px solid var(--type-color);
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: var(--white);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: var(--black);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    display: block;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: var(--type-color);
    color: var(--black);
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 60%);
            transform: translate(25%, 60%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: var(--white);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: var(--white);
    padding: 14px 20px;
    border: 1px solid var(--white);
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: var(--type-color);
    color: var(--black);
    transition: 250ms;
  }
}

.hero-container {
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
  object-fit: contain;
  background-color: var(--black);
}

.hero-container > h1 {
  color: var(--white);
  font-size: 80px;
  margin-top: -100px;
  z-index: 1;
}

.hero-container > p {
  margin-top: 8px;
  color: var(--white);
  font-size: 48px;
  /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif; */
  font-family: 'Open Sans', sans-serif;
  z-index: 1;
}

.hero-wave-back {
  position: absolute;
  bottom: 0;
  margin-bottom: -2%;
  height: 35%;
  opacity: 0.45;
}

.hero-wave-mid {
  position: absolute;
  bottom: 0;
  margin-bottom: -2%;
  height: 25%;
  opacity: 0.7;
}

.hero-wave-front {
  position: absolute;
  bottom: 0;
  margin-bottom: -2%;
  height: 20%;
  opacity: 1;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }

  .hero-wave-back {
    margin-bottom: -5%;
  }

  .hero-wave-mid {
    margin-bottom: -5%;
  }

  .hero-wave-front {
    margin-bottom: -5%;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }

  .hero-wave-back {
    margin-bottom: -5%;
  }

  .hero-wave-mid {
    margin-bottom: -5%;
  }

  .hero-wave-front {
    margin-bottom: -5%;
  }
}

:root {
  --priamry: var(--white);
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--outline {
  background-color: transparent;
  color: var(--white);
  padding: 8px 20px;
  border: 1px solid var(--white);
  border: 1px solid var(--priamry);
  transition: all 0.3s ease-out;
}

.btn--outline:hover {
  color: var(--type-color);
  border: 1px solid var(--type-color);
  transition: all 0.2s ease-out;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover {
  background: var(--white);
  color: var(--black);
  transition: all 0.3s ease-out;
}

/* Handles styling for the typed word effect */
.word span {
  color: var(--type-color);
  position: relative;
}

.word span::after {
  content: '';
  width: 8px;
  height: 100%;
  background: var(--type-color);
  display: block;
  position: absolute;
  right: -10px;
  top: 0;
  animation: blink 0.5s ease infinite alternate-reverse;
}

@-webkit-keyframes blink {
  from {
    opacity: 100%;
  }

  to {
    opacity: 0%;
  }
}

@keyframes blink {
  from {
    opacity: 100%;
  }

  to {
    opacity: 0%;
  }
}

.word.empty {
  visibility: hidden;
}

.word.empty span::after {
  visibility: visible;
  right: 0;
}

.about {
  padding: 4rem;
  background: var(--white);
}

.primary_container {
  display: flex;
  flex-flow: row;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.img_container {
  justify-content: left;
  max-width: 40%;
  max-height: 30%;
  height: auto;
  overflow: hidden;
  border-radius: 15px;
}

.text_wrapper {
  justify-content: right;
  padding-left: 8%;
  width: 100%;
}

.title_container {
  float: top;
  padding-bottom: 5%;
  font-size: 30px; /* 300% */
  color: var(--black);
}

.description_container {
  float: bottom;
  padding-bottom: 5%;
  font-size: 20px; /* 125 % */
  color: var(--black);
}

.contact-container {
  display: flex;
  flex-flow: column;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.icon-row {
  display: flex;
  justify-content: space-between;
  padding-top: 5%;
  width: 50%;
}

.icon-link-wrapper {
  width: 15%;
}

.icon-wrapper {
  width: 100%;
  height: auto;
}

.icon-wrapper:hover {
  opacity: 0.6;
  transition: all 0.2s ease-out;
}

/* Resume Button stuff below */
:root {
  --res-priamry: var(--black);
}

.res-btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  width: 100%;
}

.res-btn--outline {
  background-color: var(--black);
  color: var(--white);
  padding: 8px 20px;
  border: 1px solid var(--black);
  border: 1px solid var(--res-priamry);
  transition: all 0.3s ease-out;
}

.res-btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.res-btn--large:hover {
  background: transparent;
  color: var(--black);
  transition: all 0.3s ease-out;
}

/* Handle small screen stuff  (Vertical)
This applies when screen (long side) is smaller than 812 pixels 
AKA iPhones 5 - iPhone X
*/
@media only screen and (max-width: 812px) and (orientation: portrait) {
  .primary_container {
    display: flex;
    flex-flow: column;
  }

  .img_container {
    justify-content: top;
    max-width: 100%;
  }

  .text_wrapper {
    justify-content: bottom;
    width: 120%;
  }

  .title_container {
    float: top;
    padding-bottom: 8%;
    padding-top: 8%;
    font-size: 150%;
    color: var(--black);
  }

  .description_container {
    float: bottom;
    padding-bottom: 10%;
    font-size: 110%;
    color: var(--black);
  }

  .icon-row {
    width: 80%;
  }

  .res-btn {
    width: 50%;
  }

  .icon-link-wrapper {
    width: 20%;
    padding-top: 5%;
  }
}

/* Same phones as above but horizontal (long side is 812 pixels or less) */
@media only screen and (max-width: 812px) and (orientation: landscape) {
  .img_container {
    max-width: 50%;
  }

  .text_wrapper {
    width: 120%;
  }

  .title_container {
    float: top;
    padding-bottom: 5%;
    padding-top: 5%;
    font-size: 150%;
    color: var(--black);
  }

  .description_container {
    float: bottom;
    padding-bottom: 10%;
    font-size: 90%;
    color: var(--black);
  }

  .icon-row {
    width: 80%;
  }

  .res-btn {
    width: 60%;
  }

  .icon-link-wrapper {
    width: 20%;
    padding-top: 5%;
  }
}

/*
Handle iPad (Vertical) where long side is 1000 pixels or more
*/
@media only screen and (min-height: 1000px) and (orientation: portrait) {
  .img_container {
    justify-content: top;
    max-width: 45%;
  }

  .text_wrapper {
    width: 120%;
  }

  .title_container {
    float: top;
    padding-bottom: 5%;
    padding-top: 5%;
    font-size: 250%;
    color: var(--black);
  }

  .description_container {
    float: bottom;
    padding-bottom: 10%;
    font-size: 125%;
    color: var(--black);
  }

  .res-btn {
    width: 50%;
  }

  .icon-link-wrapper {
    width: 23%;
    padding-top: 5%;
  }
}

/* Handle iPad (Horizontal) where long side is 1000 pixels or more */
@media only screen and (min-width: 1000px) and (orientation: landscape) {
  .res-btn {
    width: 50%;
  }

  .res-btn--large {
    font-size: 150%;
  }
}

.titleContainer {
  --b: 3.5px; /* thickness of the border */
  --c: var(--black); /* color of the border */
  --w: 20px; /* width of border */

  border: var(--b) solid #0000; /* space for the border */
  --_g: #0000 90deg, var(--c) 0;
  --_p: var(--w) var(--w) border-box no-repeat;

  /* Each conic-gradient produces one of the four corners.
  *  In order: top-left, top-right, bottom-left, bottom-right
  */
  background: conic-gradient(
        from 90deg at top var(--b) left var(--b),
        var(--_g)
      )
      0 0 / var(--_p),
    /* conic-gradient(from 180deg at top var(--b) right var(--b), var(--_g)) 100% 0 /
      var(--_p), 
      conic-gradient(from 0deg at bottom var(--b) left var(--b), var(--_g)) 0
      100% / var(--_p), */
      conic-gradient(from -90deg at bottom var(--b) right var(--b), var(--_g))
      100% 100% / var(--_p);

  width: 14%;
  height: 8vh;
  margin: 0 auto;
  display: flex;
  flex-flow: column;
  box-sizing: border-box;
  font-size: 40px;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 3%;
  border-radius: 5px;
}

/* Handle small screen stuff  (Vertical)
This applies when screen (long side) is smaller than 900 pixels 
AKA iPhones 5 - iPhone X
*/
@media only screen and (max-width: 850px) and (orientation: portrait) {
  .titleContainer {
    width: 75%;
    margin-bottom: 8%;
  }
}

/* Same phones as above but horizontal (long side is 900 pixels or less) */
@media only screen and (max-width: 900px) and (orientation: landscape) {
  .titleContainer {
    height: 12vh;
    width: 35%;
    margin-bottom: 6%;
  }
}

/*
  Handle iPad (Vertical) where long side is 1000 pixels or more
  */
@media only screen and (min-height: 1000px) and (orientation: portrait) {
  .titleContainer {
    width: -webkit-fit-content + 1;
    width: -moz-fit-content + 1;
    width: fit-content + 1;
    margin-bottom: 10%;
  }
}

/* Handle iPad (Horizontal) where long side is 1000 pixels or more */
@media only screen and (min-width: 1000px) and (orientation: landscape) {
  .titleContainer {
    width: 20%;
    margin-bottom: 6%;
  }
}

.skills-container {
  padding: 4rem;
  background: var(--white);
}

.skills-section {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin: 12px 0px;
}

.skills-section-title {
  margin-left: 2%;
  font-size: 36px;
  width: 15%;
  height: 100%;
  align-self: center;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 60%;
  justify-content: center;
}

/* Styling for individual card items*/
.skill-card {
  width: -webkit-max-content;
  width: max-content;
  padding: 8px 16px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  border-radius: 8px;
}

.skill-icon {
  font-size: 80px;
}

.skill-name {
  font-size: 18px;
}

/* iPhone vertical */
@media only screen and (max-width: 1024px) and (orientation: portrait) {
  .skills-section-title {
    padding-bottom: 3%;
    margin-right: 10%;
  }
  .skill-card {
    width: -webkit-max-content;
    width: max-content;
    height: 100px;
  }
  .skill-icon {
    font-size: 60px;
  }
}

@media only screen and (max-width: 720px) {
  .skills-section-title {
    padding-bottom: 3%;
  }
  .skills-list {
    max-width: 100%;
    justify-content: center;
  }
  .skills-section {
    flex-direction: column;
    align-items: center;
  }
}

.cards {
  padding: 4rem;
  background: var(--white);
}

h1 {
  text-align: center;
  color: var(--black);
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 25px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: var(--white);
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
  transition: all 0.75s ease;
}

.cards__item__img:hover ~ .cards__item__pic-wrap::after {
  visibility: hidden;
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__title {
  color: var(--black);
  font-size: 24px;
  line-height: 40px;
}

.cards__item__text {
  color: var(--black);
  font-size: 16px;
  line-height: 24px;
  font-weight: lighter;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}

